<template>
  <div class="personal-content-body">
    <div class="fundRun-box">
      <div class="fundRun-top-box clear">
        <div class="left breadcrumb">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item v-if="type==1" :to="{ path: '/home/fundManage?page=' + page }">基金数据</el-breadcrumb-item>
            <el-breadcrumb-item v-if="type==2" :to="{ path: '/home/fundManageSr' }">基金数据</el-breadcrumb-item>
            <el-breadcrumb-item>{{this.fundName}}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <span>{{this.fundName}}</span>
      </div>
      <!-- <div class="fundRun-body-box">
        <fund-report-list></fund-report-list>
      </div> -->
      <div class="fundRun-body-box">
        <div class="fundRun-body-title clear">
          <em v-if="type==1">基金运行监测报表</em>
          <em v-if="type==2">已投项目情况</em>
          <div class="right" @click="downloadFundFile">
            <div class="table-group-title-btn btn-blue">
              <span class="icon_download"></span>下载基金模版
            </div>
          </div>
          <div class="right">
            <label for="upload-fund-input">
              <div class="table-group-title-btn btn-blue">
                <span class="icon_upload"></span>Excel导入
              </div>
            </label>
            <input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadFundExcel($event)" />
          </div>
          <!-- <div class="right" @click="toDownloadExcel" v-if="isShow"> -->
          <div class="right" @click="toDownloadExcel" >
            <div class="table-group-title-btn btn-blue">
              <span class="icon_download"></span>Excel导出
            </div>
          </div>
        </div>
        <!-- 切换nav -->
        <div class="fundRun-body-nav clear" v-if="type==1">
          <div class="fundRun-body-nav-box">
            <div class="fundRun-body-nav-item" :class="{'click':step==item.step}" v-for="(item,index) of navList" :key="index" @click="toCutnav(item.step)">
              {{item.name}}
            </div>
          </div>
        </div>
        <div class="fundRun-body-tab-box" v-if="step==1">
          <div class="fundRun-body-tab-btn-box fundRun-body-tab-btn-box1 clear" v-if="!noData1">
            <div class="fundRun-body-tab-btn right" @click="toEditor" v-if="!editor">
              <img src="../../images/projectpermissions_icon1.png" alt="">
              <span>编辑</span>
            </div>
          </div>
          <table class="table-normal qynb-table-normal">
            <!-- 头部 -->
            <tr class="noColspanTr" v-if="!noData1">
              <th width='300'>项目</th>
              <th>初期累计金额</th>
              <th>1季度</th>
              <th>2季度</th>
              <th>3季度</th>
              <th>4季度</th>
              <th width='217'>期末累计数/时点数</th>
            </tr>
            <!-- 内容 -->
            <tr class="noColspanTr" v-for="(item,index) of stepOneData " :key="index" :class="{'secondData': searchIndex(item.pid)}" v-show="searchToShow(item.pid)">
              <td width='300'>
                <span>{{item.zhName}}</span>
              </td>
              <td v-for="(item1,index1) of item.contents " :key="index1">
                <input type="text" placeholder="请输入" v-model="item1.contents" v-if="editor" @change="getChangeData(item1.contents,item1.sid,item1.fmid)">
                <span v-else>{{item1.contents!=''?item1.contents:'- -'}}</span>
                <span class="showIcon right" :class="{'revolveShowIcon':searchToShow(item.id)}" @click="showChildren(item.id)" v-if="searchIndex(item.id,index1)"></span>
              </td>
            </tr>
          </table>
          <div class="clear" v-if="editor">
            <div class="cancle-btn right" @click="toConmmit()">提交</div>
            <div class="cancle-btn right" @click="toCancle()">取消</div>
          </div>
          <div class="table-nodata" v-if="noData1">
            <img src="../../images/table_nodata.png"> 没有对应数据,请先导入Excel
          </div>
        </div>
        <div class="fundRun-body-tab-box" v-if="step==2">
          <div class="fundRun-body-tab-item-box" v-for="(first,firstIndex) of stepTwoData " :key="firstIndex">
            <div class="fundRun-body-tab-btn-box clear">
              <div class="fundRun-body-tab-btn right" @click="toEditor(first.id,first)" v-if="!searchEditorId(first.id)">
                <img src="../../images/projectpermissions_icon1.png" alt="">
                <span>编辑</span>
              </div>
            </div>
            <div class="fundRun-body-tab-title">
              <span class="cricle"></span>
              <span class="fundRun-body-tab-title-name"> {{first.zhName}}</span>
            </div>
            <table class="table-normal qynb-table-normal">
              <!-- 头部 -->
              <tr class="noColspanTr">
                <th width='300'>项目</th>
                <th>初期累计金额</th>
                <th>1季度</th>
                <th>2季度</th>
                <th>3季度</th>
                <th>4季度</th>
                <th width='217'>期末累计数/时点数</th>
              </tr>
              <!-- 内容 -->
              <tr class="noColspanTr" v-for="(item,index) of first.child " :key="index" :class="{'secondData': searchIndex(item.pid)}" v-show="searchToShow(item.pid)">
                <td width='300'>
                  <span>{{item.zhName}}</span>
                </td>
                <td v-for="(item1,index1) of item.contents " :key="index1">
                  <input type="text" placeholder="请输入" v-model="item1.contents" v-if="searchEditorId(first.id)" @change="getChangeData(item1.contents,item1.sid,item1.fmid,first.id)">
                  <span v-else>{{item1.contents!=''?item1.contents:'- -'}}</span>
                  <span class="showIcon right" :class="{'revolveShowIcon':searchToShow(item.id)}" @click="showChildren(item.id)" v-if="searchIndex(item.id,index1)"></span>
                </td>
              </tr>
            </table>
            <div class="clear" v-if="searchEditorId(first.id)">
              <div class="cancle-btn right" @click="toConmmit(first.id)">提交</div>
              <div class="cancle-btn right" @click="toCancle(first.id)">取消</div>
            </div>
          </div>
          <div class="table-nodata" v-if="noData2">
            <img src="../../images/table_nodata.png"> 没有对应数据,请先导入Excel
          </div>
        </div>
        <div class="fundRun-body-tab-box" v-if="step==3">
          <fund-lead :fundId="fundId"></fund-lead>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getFundExcel,
  getNewFundExcel,
  fundExcel,
  fundExcel2,
  downloadExcel,
  downloadExcel2
} from '../../config/api'
import api from '../../config/url'; //引用url.js
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import fundLead from '../../pages/fundrunComponent/fundLead.vue'
import fundReportList from '../../pages/fundrunComponent/fundReportList.vue'
export default {
  data() {
    return {
      type: 1, //1=>其他公司 2=>尚融
      fundName: '',
      fundId: '',
      noData1: false,
      noData2: false,
      selectTime: '', //搜索的时间
      editor: false, //编辑状态
      selectId: '',
      toShowArrOne: [], //基金资产和费用信息 二级所属行的id的数组
      showListStatusArrOne: [], //基金资产和费用信息 二级所属行的id的数组
      toShowArrTwo: [], //基金投资信息 二级所属行的id的数组,
      showListStatusArrTwo: [], //基金投资信息 二级所属行的id的数组,
      editorArrOne: [], //基金资产和费用信息 编辑模块的id数组
      editorArrTwo: [], //基金投资信息 编辑模块的id数组
      showEditorArrTwo: [], //基金投资信息 编辑模块的id数组
      isShow: false, //
      step: 1,
      stepOneData: [],
      copyStepOneData: [],
      stepTwoData: [],
      navList: [{
        name: '基金资产和费用信息',
        step: 1,
      }, {
        name: '基金投资信息',
        step: 2
      }, {
        name: '已投项目情况',
        step: 3
      }],
      record: [],
      copyArr: [],
      hid: '',
      page: 1,
    }
  },
  components: { fundLead, fundReportList },
  watch: {
    $route(to, from) {
      this.init();
    }
  },
  created() {
  	if (this.$route.query.page) {
  		this.page = this.$route.query.page;
  	}
    this.init();
  },
  computed: {},
  mounted() {},
  methods: {
    init() {
      let routename = this.$route.name;
      if (routename == 'fundRun') {
        this.type = 1;
      } else if (routename == 'fundRunSr') {
        this.type = 2;
        this.step = 3
      }
      if (this.$route.query.id != undefined) { //编辑
        this.fundName = decodeURIComponent(this.$route.query.name);
        this.fundId = this.$route.query.id
      }
      this.initialData();
    },
    //切换
    toCutnav(step) {
      this.step = step;
      this.noData1 = false;
      this.noData2 = false;
      this.record = [];
      if (step == 1 || 2) {
        this.initialData();
      }
    },


    //展示下拉
    showChildren(id) {
      let showArr = [];
      if (this.step == 1) {
        showArr = this.showListStatusArrOne;
      } else if (this.step == 2) {
        showArr = this.showListStatusArrTwo;
      }
      let index = showArr.indexOf(id);
      if (index == -1) {
        showArr.push(id);
      } else {
        showArr.splice(index, 1)
      }
    },
    //搜索是否已经展开
    searchToShow(pid) {
      let showArr1 = [];
      let showArr2 = [];
      if (this.step == 1) {
        showArr1 = this.showListStatusArrOne;
        showArr2 = this.toShowArrOne;
      } else if (this.step == 2) {
        showArr1 = this.showListStatusArrTwo;
        showArr2 = this.toShowArrTwo;
      }
      if (showArr2.indexOf(pid) == -1) {
        return true;
      } else {
        if (showArr1.indexOf(pid) == -1) {
          return false
        } else {
          return true;
        }
      }
    },
    //搜索是否存在
    searchIndex(pid, index1) {
      let showArr1 = [];
      let showArr2 = [];
      if (this.step == 1) {
        showArr1 = this.showListStatusArrOne;
        showArr2 = this.toShowArrOne;
      } else if (this.step == 2) {
        showArr1 = this.showListStatusArrTwo;
        showArr2 = this.toShowArrTwo;
      }
      if (showArr2.indexOf(pid) != -1) {
        if (index1 != undefined) {
          if (index1 == 5) {
            return true
          } else {
            return false;
          }
        } else {
          return true;
        }
      } else {
        return false;
      }
    },
    //去编辑
    toEditor(id, data) {
      if (this.step == 1) {
        this.editor = !this.editor;
        this.copyStepOneData = JSON.parse(JSON.stringify(this.stepOneData))
      } else if (this.step == 2) {
        let index = this.showEditorArrTwo.indexOf(id);
        if (index == -1) {
          this.showEditorArrTwo.push(id);
        } else {
          this.showEditorArrTwo.splice(index, 1)
        }
        //储存原来的值
        let newObj = {
          id: id,
          data: JSON.parse(JSON.stringify(data.child))
        }
        this.copyArr.push(newObj);
      }
    },
    toCancle(id) {
      if (this.step == 1) {
        this.editor = !this.editor;
        this.stepOneData = JSON.parse(JSON.stringify(this.copyStepOneData));
        this.record = [];
      } else if (this.step == 2) {
        let index = this.showEditorArrTwo.indexOf(id);
        if (index == -1) {
          this.showEditorArrTwo.push(id);
        } else {
          this.showEditorArrTwo.splice(index, 1)
        }
        //获取储存的值
        let copyData = [];
        this.copyArr.forEach((item4, index) => {
          if (item4.id == id) {
            copyData = JSON.parse(JSON.stringify(item4.data));
            this.copyArr.splice(index, 1);
          }
        });
        //赋值变回原来的
        this.stepTwoData.forEach((item5, index) => {
          if (item5.id == id) {
            item5.child = copyData;
          }
        });
        //清掉改变的记录
        this.record.forEach((item1, index) => {
          if (item1.id == id) {
            this.record.splice(index, 1);
          }
        });
      }
    },
    toConmmit(id) {
      let NewRecord = [];
      if (this.step == 1) {
        NewRecord = this.record;
      } else {
        this.record.forEach(element => {
          if (element.id == id) {
            let newObj = {
              sid: element.sid,
              fmid: element.fmid,
              contents: element.contents,
            }
            NewRecord.push(newObj);
          }
        });
      }

      if (NewRecord.length == 0) {
        dialogConfirm('info', '请修改后再提交!');
        return false;
      }
      getNewFundExcel({
        flag: this.step,
        hid: this.hid,
        label: 2,
        record: NewRecord,
        fundId: this.fundId,
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '修改成功');
          if (this.step == 1) {
            this.editor = !this.editor;
            this.record = [];
          } else if (this.step == 2) {
            //返回非编辑状态
            let index = this.showEditorArrTwo.indexOf(id);
            if (index == -1) {
              this.showEditorArrTwo.push(id);
            } else {
              this.showEditorArrTwo.splice(index, 1)
            }
            //删除已提交变化
            this.record.forEach((item1, index) => {
              if (item1.id == id) {
                this.record.splice(index, 1);
              }
            });
            this.copyArr.forEach((item4, index) => {
              if (item4.id == id) {
                this.copyArr.splice(index, 1);
              }
            });
          }
          this.initialData();
        } else {
          dialogConfirm('info', res.data.message);
        }
      })
    },

    searchEditorId(firstId) {
      if (this.showEditorArrTwo.indexOf(firstId) != -1) {
        return true;
      } else {
        return false;
      }
    },

    //初始化数据
    initialData() {
      getNewFundExcel({
        flag: this.step,
        fundId: this.fundId,
        label: 1
      }).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload.data;
          this.hid = res.data.payload.hid
          if (this.hid == null || this.hid == undefined || this.hid == '') {
            this.isShow = false;
          } else {
            this.isShow = true;
          }
          if (this.step == 1) {
            data.forEach((item, index) => {
              if (item.child != undefined && item.child.length != 0) {
                this.toShowArrOne.push(item.id);
                this.showListStatusArrOne.push(item.id);
                let childArr = item.child;
                childArr.unshift(index + 1, 0);
                Array.prototype.splice.apply(data, childArr);
              }
            });
            this.stepOneData = data;
          } else if (this.step == 2) {
            data.forEach((item, index) => {
              if (item.child != undefined && item.child.length != 0) {
                item.child.forEach((item1, index1) => {
                  if (item1.child != undefined && item1.child.length != 0) {
                    this.toShowArrTwo.push(item1.id);
                    this.showListStatusArrTwo.push(item.id);
                    let childArr = item1.child;
                    childArr.unshift(index1 + 1, 0);
                    Array.prototype.splice.apply(item.child, childArr);
                  }
                })
              }
            });
            this.stepTwoData = data;
          }
        } else {
          if (this.step == 1) {
            this.noData1 = true;
            this.noData2 = false;
          } else {
            this.noData1 = false;
            this.noData2 = true;
          }
        }
      })
    },
    getChangeData(contents, sid, fmid, id) {
      let newObj = {
        sid: sid,
        contents: contents,
        fmid: fmid
      }
      if (this.step == 2) {
        newObj.id = id;
      }
      let notHave = true;
      this.record.forEach(item => {
        if (item.fmid == fmid) {
          item.contents = contents;
          notHave = false;
        }
      });
      if (notHave) {
        this.record.push(newObj);
      }
    },

    //导入Excel文件
    uploadFundExcel(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      if (file == undefined) {
        return false;
      }
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      if (this.type == 1) {
        fundExcel({
          fund_id: this.$route.query.id
        }, file).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '导入成功');
            this.toCutnav(this.step);
            this.initialData();
            inputDOM.value = '';
          } else {
            dialogConfirm('info', res.data.message);
            inputDOM.value = '';
          }
        })
      } else if (this.type == 2) {
        fundExcel2({
          fundId: this.$route.query.id
        },file).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '导入成功');
            this.toCutnav(this.step);
            this.initialData();
            inputDOM.value = '';
          } else {
            dialogConfirm('info', res.data.message);
            inputDOM.value = '';
          }
        })
      }

    },
    //导出Excel文件
    toDownloadExcel() {
      let table_token =   window.localStorage.getItem("htcx_server_table_token");
      window.location.href = api.url + '/web/fund/exportExcel?projId=' + this.$route.query.id+'&table_token='+table_token;
    },
    //下载模版
    downloadFundFile() {
      if (this.type == 1) {
        downloadExcel().then(res => {
          if (res.data.msgNo == 1) {
            window.location.href = res.data.payload;
          } else {
            dialogConfirm('info', res.data.message)
          }
        });
      } else if (this.type == 2) {
        downloadExcel2().then(res=>{
          if (res.data.msgNo == 1) {
            window.location.href = res.data.payload;
          } else {
            dialogConfirm('info', res.data.message)
          }
        })
      }

    },
  }
}

</script>
