        top: 50%;
        right: 18px;
        margin-top: -3px;
        transition: all 200ms linear;
        cursor: pointer;
      }
      .revolveShowIcon {
        transform: rotate(180deg)
      }
    }
    .table-normal>.secondData {
      background-color: #F6FBFF;
    }
    .table-normal>.secondData>td:nth-child(1) {
      &>span {
        padding-left: 30px;
      }
    }
  }
  .commit-btn-box {
    margin-top: 30px;
    .commit-btn {
      display: inline-block;
      width: 100px;
      height: 30px;
      font-size: 14px;
      text-align: center;
      line-height: 30px;
      background-color: #3C98E8;
      color: white;
    }
  }
  .fundRun-body-tab-item-box {
    margin-bottom: 20px;
  }
} //excel文件导入导出按钮
.table-group-title-btn {
  line-height: 30px;
  font-size: 14px;
  padding: 0px 10px;
  margin-right: 0px;
  margin-left: 10px;
  position: relative;
  top: 0px;
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
    float: left;
    margin-top: 5px;
    margin-right: 5px;
  }
  .icon_upload {
    background: url('../../images/icon_upload.png') no-repeat;
    background-size: 90%;
  }
  .icon_download {
    background: url('../../images/icon_download.png') no-repeat;
    background-size: 100%;
    margin-top: 4px;
  }
}

.fundRun-body-tab-btn {
  display: flex;
  width: 61px;
  height: 23px;
  align-items: center;
  background-color: #3C98E8;
  justify-content: center;
  position: relative;
  top: 22px;
  cursor: pointer;
  img {
    width: 14px;
    height: 14px;
    margin-right: 2px;
  }
  span {
    color: #ffffff;
    font-size: 14px;
  }
}

.fundRun-body-tab-btn-box1 {
  margin-bottom: 10px;
  .fundRun-body-tab-btn {
    top: 0px;
  }
}

</style>



